.quality-section {
  padding-top: 60px;
  padding-bottom: 60px;
  background-color: #f8f8f8;
}
.quality-list {
  display: flex;
  gap: 60px;
  justify-content: center;
}
.quality-text {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.02em;
  color: #2e2f42;
}
.quality-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.quality-icon {
  display: block;
  width: 32px;
  height: 32px;
  margin-bottom: 14px;
}
